
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Box from 'vux/src/components/box/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XImg from 'vux/src/components/x-img/index.vue'

export default {
components: {
XHeader,
XImg,
Box,
XInput,
XButton,
Group,
Cell,
CellBox,
XSwitch
},
data () {
return {
showLoading: false,
foodSaName: '',
foodSaCard: '',
showCardpicA: false,
showCardpicB: false,
showCardpicC: false,
showCardpicD: false,
hasPicA: false,
hasPicB: false,
hasPicC: false,
hasPicD: false,
srcA: '',
srcB: '',
srcC: '',
srcD: ''
}
},
mounted () {
},
computed: {},
methods: {
conformSa: function () {
this.showLoading = true;
let _this = this;
if (this.hasPicA && this.hasPicB && this.hasPicC && this.hasPicD) {
this.$http.post('http://39.108.143.172:8080/get', {}).then((res) => {
console.log(res)
});
this.$vux.alert.show({
title: '提交成功',
content: '管理员审核通过后请重新登陆～',
onShow () {
},
onHide () {
_this.$router.push({ path: '/firstLogin' })
}
})
} else {
this.$vux.alert.show({
title: '出错啦',
content: '请检查材料是否齐全～',
onShow () {
},
onHide () {
_this.showLoading = false;
}
})
}
},
showCard: function (type) {
if (type === 'A') {
this.showCardpicA = !this.showCardpicA
} else if (type === 'B') {
this.showCardpicB = !this.showCardpicB
} else if (type === 'C') {
this.showCardpicC = !this.showCardpicC
} else if (type === 'D') {
this.showCardpicD = !this.showCardpicD
}
},
initSrc: function (event) {
let _this = this;
let file = event.currentTarget.files[0];
let reader = new FileReader();
reader.onloadend = function () {
if (event.target.name === 'cardA') {
_this.hasPicA = true;
_this.srcA = reader.result;
} else if (event.target.name === 'cardB') {
_this.hasPicB = true;
_this.srcB = reader.result;
} else if (event.target.name === 'cardC') {
_this.hasPicC = true;
_this.srcC = reader.result;
} else if (event.target.name === 'cardD') {
_this.hasPicD = true;
_this.srcD = reader.result;
}
};
if (file) {
reader.readAsDataURL(file);
}
}
}
}



